<template>
  <a-form :model="form" layout="vertical">
    <div>
      <a-row :gutter="24">
        <a-col :span="12">
          <a-form-item label="角色名" :name="form.userName">
            <a-input v-model:value="form.userName" placeholder="请输入角色名" />
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item label="备注" :name="form.userName">
            <a-input v-model:value="form.userName" placeholder="请输入备注" />
          </a-form-item>
        </a-col>
        <a-col :span="24">
          <a-form-item label="菜单权限" :name="form.userName">
            <div class="role-tree">
              <!-- 树节点 -->
              <a-tree
                checkable
                :tree-data="treeData"
                @check="onCheck"
                :defaultCheckedKeys="defaultCheckedKeys"
              />
            </div>
          </a-form-item>
        </a-col>
      </a-row>
    </div>
  </a-form>
</template>
<script>
import store from "@/store";

export default {
  data() {
    return {
      form: {
        userName: "",
        status: false,
        role: undefined,
        password: "",
        confirmPassword: "",
      },

      defaultCheckedKeys: [],
    };
  },
  computed: {
    treeData() {
      // 递归转换字段

      const routes = this.transformTree(store.state.app.routers);

      return routes;
    },
  },
  methods: {
    onCheck(e) {
      this.defaultCheckedKeys = e;
    },
    transformTree(routes) {
      return routes.map((item) => {
        if (item.children) {
          item.children = this.transformTree(item.children);
        }
        return {
          title: item.meta.title,
          key: item.path,
          ...item,
        };
      });
    },
  },
};
</script>
<style>
.avatar-uploader > .ant-upload {
  width: 128px;
  height: 128px;
}
.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}

.role-tree {
  padding: 20px 0;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  height: 300px;
  overflow: auto;
}
</style>
